.blind {
	overflow: hidden;
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	line-height: 0;
	white-space: normal;
	word-wrap: break-word;
	word-break: break-all;
	clip: rect(0, 0, 0, 0);
}
.blind,
legend {
	overflow: hidden;
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	line-height: 0;
	white-space: normal;
	word-wrap: break-word;
	word-break: break-all;
	clip: rect(0, 0, 0, 0);
}

.App {
	position: relative;
	max-width: 640px;
	margin: 0 auto;
	overflow: hidden;
	color: #2f332f;
	font-size: 1.5rem;
}

.App h3 {
	margin: 0;
	padding: 30px 0;
	background-color: #3b574f;
	text-align: center;
	color: #eee;
}

.Random-result-popup {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 2;
	padding: 40px 0;
	background-color: rgba(0, 0, 0, 0.7);
	font-size: 0;
	color: #fff;
	text-align: center;
	visibility: hidden;
	opacity: 0;
	-webkit-transition: 200ms ease;
	transition: 200ms ease;
	overflow: auto;
}

.Random-result-popup:after {
	display: inline-block;
	width: 0;
	height: 100%;
	vertical-align: middle;
	content: "";
}

.Random-result-popup.show {
	opacity: 1;
	visibility: visible;
}

.Random-result-inner {
	position: relative;
	display: inline-block;
	width: 80%;
	margin: 20px auto;
	padding: 50px 20px;
	border: 1px solid #bbb;
	background: #fff;
	box-sizing: border-box;
	text-align: center;
	vertical-align: middle;
}

.Random-result-popup strong {
	display: block;
	margin-bottom: 20px;
	padding-bottom: 10px;
	border-bottom: 2px solid #222;
	color: #222;
	font-size: 28px;
}

.Random-result-popup ul {
	margin: 0;
	padding: 0;
	color: #222;
	font-weight: bold;
	font-size: 20px;
	line-height: 30px;
	list-style: none;
}

.Random-result-close {
	position: absolute;
	top: -25px;
	right: 10px;
	width: 50px;
	height: 50px;
	border: 1px solid #bbb;
	border-radius: 50%;
	background-color: #fff;
}

.Random-result-close:before,
.Random-result-close:after {
	content: "";
	width: 30px;
	height: 1px;
	position: absolute;
	left: 50%;
	top: 50%;
	border-radius: 4px;
	background: #000;
}
.Random-result-close:before {
	transform: translate(-50%, -50%) rotate(-45deg);
}
.Random-result-close:after {
	transform: translate(-50%, -50%) rotate(45deg);
}
